import React from 'react'

class Login extends React.Component{
	render(){
		return (
			<form className='cent'>
			   <div className='log_inp'>
			    <input className='top_right_left' type='text' placeholder='username or email'/>
			    
			    <input type='text'  placeholder='password'/>
			    
			    <input className='bottom_right_left' type='text'  placeholder='user token'/>
			    
               </div>
               <br/><br/>
               <div className='log' onClick={this.props.change}>log in</div>
               <hr />
			</form>
		)
	}
}

export default Login